body {
    font-size: 1em;
    background-color: #f4f4f4;
    color: #333;
}

.dark-mode {
    --background-color: #333333;
    --text-color: #ffffff;
}

nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}

nav ul li {
    float: left;
}

nav ul li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

nav ul li a:hover {
    background-color: #111;
}

#main-header {
    background-color: #333;
    color: #fff;
    padding: 20px;
    text-align: center;
}

.container {
    width: 80%;
    margin: auto;
    overflow: hidden;
}

footer {
    background-color: #333;
    color: #fff;
    text-align: center;
    padding: 10px 0;
    position: fixed;
    bottom: 0;
    width: 100%;
}

main {
    padding: 20px;
}

section {
    margin-bottom: 20px;
}

section img {
    max-width: 100%;
    height: auto;
    display: block;
    margin: 10px 0;
}

a {
    color: #333;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

.button {
    display: inline-block;
    padding: 10px 20px;
    margin: 20px 0;
    background-color: #4CAF50;
    color: white;
    text-align: center;
    border-radius: 5px;
    text-decoration: none;
}

.button:hover {
    background-color: #45a049;
}

#edit-profile-section,
#schedule-section,
#match-section {
    max-width: 600px;
    margin: 0 auto;
    background: #fff;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

#edit-profile-section h2,
#schedule-section h2,
#match-section h2 {
    margin-bottom: 10px;
    font-size: 24px;
}

#edit-profile-section p,
#schedule-section p {
    margin-bottom: 20px;
    color: #666;
}

#edit-profile-section form label,
#schedule-section form label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
}

#edit-profile-section form input,
#schedule-section form textarea {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

#edit-profile-section form button,
#schedule-section form button {
    width: 100%;
    padding: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

#edit-profile-section form button:hover,
#schedule-section form button:hover {
    background-color: #45a049;
}

.user-profile {
    border: 1px solid #ccc;
    padding: 20px;
    margin-bottom: 20px;
    text-align: center;
    background-color: #fff;
    border-radius: 5px;
}

.user-profile img {
    width: 100%;
    height: auto;
    margin-bottom: 20px;
}

.user-profile h2 {
    margin: 10px 0;
}

.badge {
    display: inline-block;
    padding: 5px 10px;
    margin: 5px;
    background-color: #4CAF50;
    color: white;
    border-radius: 5px;
}

.badge.unavailable {
    background-color: #f44336;
}

.looking-for {
    margin: 20px 0;
}

.looking-for p {
    font-weight: bold;
}

.looking-for ul {
    list-style-type: none;
    padding: 0;
}

.looking-for ul li {
    display: inline-block;
    margin-right: 10px;
    background-color: #ddd;
    padding: 5px 10px;
    border-radius: 5px;
}

.buttons {
    margin-top: 20px;
}

.interested-button,
.next-button {
    width: 45%;
    padding: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 5px;
}

.interested-button:hover,
.next-button:hover {
    background-color: #45a049;
}

.next-button {
    background-color: #ccc;
}

.next-button:hover {
    background-color: #bbb;
}

.image-container {
    position: relative;
    display: inline-block;
}

.image-caption {
    display: none;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
    text-align: center;
    padding: 10px;
}

.image-container:hover .image-caption {
    display: block;
}


/* Media queries for responsiveness */
@media only screen and (max-width: 600px) {
    nav ul li {
        float: none;
        width: 100%;
    }

    .container, #edit-profile-section, #schedule-section, #match-section {
        width: 100%;
        padding: 0 10px;
    }

    .button {
        width: 100%;
        text-align: center;
    }

    .user-profile {
        width: 100%;
    }

    .interested-button,
    .next-button {
        width: 100%;
        margin: 10px 0;
    }
}